import { Injectable } from '@angular/core';
import { EnquiriesState } from './enquiries.state';
import { Observable, finalize } from 'rxjs';
import { Inquiry, PageMetaData } from '@core/models';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { Params } from '@angular/router';

@Injectable()
export class MyEnquiriesFacade {
	constructor(private enquiryState: EnquiriesState, private adsApi: AdsApi) {}

	getEnquiryListMetaData$(): Observable<PageMetaData> {
		return this.enquiryState.enquiryListMetaData$;
	}

	getEnquiryListMetaData(): PageMetaData {
		return this.enquiryState.getEnquiryListMetaData();
	}

	setEnquiryListMetaData(metaData: PageMetaData): void {
		this.enquiryState.setEnquiryListMetaData(metaData);
	}

	getEnquiryList$(): Observable<Inquiry[]> {
		return this.enquiryState.enquiryList$;
	}

	setEnquiryList(enquiries: Inquiry[]): void {
		this.enquiryState.setEnquiryList(enquiries);
	}

	loadEnquiryList(params: Params): void {
		this.setIsLoading(true);

		this.adsApi
			.getInquiryList(params)
			.pipe(finalize(() => this.setIsLoading(false)))
			.subscribe((enquiryListData: { data: Inquiry[]; meta: PageMetaData }) => {
				this.setEnquiryList(enquiryListData?.data);
				this.setEnquiryListMetaData(enquiryListData?.meta);
			});
	}

	getIsLoading$(): Observable<boolean> {
		return this.enquiryState.isLoadingEnquiryList$;
	}

	setIsLoading(isLoading: boolean): void {
		this.enquiryState.setIsLoadingEnquiryList(isLoading);
	}
}
