import { Injectable } from '@angular/core';
import { Inquiry, PageMetaData } from '@core/models';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable()
export class EnquiriesState {
	private readonly _enquiryList$ = new BehaviorSubject<Inquiry[]>(undefined);
	private readonly _enquiryListMetaData$ = new BehaviorSubject<PageMetaData>(undefined);
	private readonly _isLoadingEnquiryList$ = new BehaviorSubject<boolean>(false);

	get isLoadingEnquiryList$(): Observable<boolean> {
		return this._isLoadingEnquiryList$.asObservable();
	}

	setIsLoadingEnquiryList(isLoading: boolean): void {
		this._isLoadingEnquiryList$.next(isLoading);
	}

	get enquiryListMetaData$(): Observable<PageMetaData> {
		return this._enquiryListMetaData$.asObservable();
	}

	getEnquiryListMetaData(): PageMetaData {
		return this._enquiryListMetaData$.getValue();
	}

	setEnquiryListMetaData(metaData: PageMetaData): void {
		this._enquiryListMetaData$.next(metaData);
	}

	get enquiryList$(): Observable<Inquiry[]> {
		return this._enquiryList$.asObservable();
	}

	setEnquiryList(enquiries: Inquiry[]): void {
		this._enquiryList$.next(enquiries);
	}
}
